import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  CardActionArea,
  CardContent,
  CardMedia
} from "@material-ui/core";
import { CAPAS } from "../localassets";
//import MainLoader from "../MainLoader";

export default function Produto(props) {
  const useStyles = makeStyles({
    root: {
      width: props.tamanho * 1.5,
      height: props.tamanho
    }
  });
  const produto = props.dados;
  const imgurl = produto.images[0] ? produto.images[0].src : "";
  const classes = useStyles();
  let imgBG = imgurl;
  const capa = CAPAS.filter(item => {
    return item.id === produto.id;
  });
  if (capa.length === 1) {
    imgBG = capa[0].img;
  }

  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          component="img"
          alt={produto.name}
          image={imgBG}
          title={produto.name}
          height={props.tamanho}
          width="auto"
        />
        <CardContent></CardContent>
      </CardActionArea>
    </Card>
  );
}
