import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Link as RouterLink } from "react-router-dom";
import { Grid, Link, IconButton } from "@material-ui/core";
import Produto from "../produto";
import ChevronLeftRoundedIcon from "@material-ui/icons/ChevronLeftRounded";
import ChevronRightRoundedIcon from "@material-ui/icons/ChevronRightRounded";
import SwipeableViews from "react-swipeable-views";

export default function FoliaCarousel(props) {
  let produtos = useSelector(state => state.products.grouped_products);
  produtos = produtos.filter(function(produto) {
    return produto.categories[0].id === props.categoriaID;
  });
  let NomeCategoria = "";
  if (produtos.length > 0) {
    NomeCategoria = produtos[0].categories[0].name;
  }

  const styles = {
    root: {
      padding: "0 10px"
    },
    slideContainer: {
      padding: "0 10px",
      width: "auto"
    },
    slide: {
      padding: 1,
      maxWidth: 170
    },
    margins: {
      margin: 10
    }
  };
  const [slideIndex, setSlideIndex] = useState(0);

  function slidePrevious() {
    let newindex = slideIndex - 1 >= 0 ? slideIndex - 1 : produtos.length - 1;
    setSlideIndex(newindex);
  }

  function slideNext() {
    let newindex = slideIndex + 1 < produtos.length ? slideIndex + 1 : 0;
    setSlideIndex(newindex);
  }

  return (
    <Grid container spacing={1}>
      <Grid
        item
        xs={6}
        className="h1-responsive font-weight-bold my-2"
        style={{ color: "#4A4A4A!important" }}
      >
        <h2 className="text-left ">{NomeCategoria}</h2>
      </Grid>
      <Grid item xs={6}>
        <h2 className="text-right">
          <IconButton aria-label="anterior" onClick={slidePrevious}>
            <ChevronLeftRoundedIcon fontSize="large" />
          </IconButton>
          <IconButton aria-label="prÃ³ximo" onClick={slideNext}>
            <ChevronRightRoundedIcon fontSize="large" />
          </IconButton>
        </h2>
      </Grid>
      <Grid item xs={12}>
        <SwipeableViews
          style={styles.root}
          slideStyle={styles.slideContainer}
          index={slideIndex}
        >
          {produtos.map(item => (
            <Link
              key={item.id}
              style={Object.assign({}, styles.slide)}
              to="/compre-aqui"
              component={RouterLink}
            >
              <Produto dados={item} tamanho={110} link={"compre-aqui"} />
            </Link>
          ))}
        </SwipeableViews>
      </Grid>
    </Grid>
  );
}
